@media (max-width: 1200px) {
  /* header */

  .header__logo img {
    max-width: 16rem;
  }

  .header__content {
    margin-left: 7rem;
  }

  .header__menu-list {
    gap: 4rem;
  }

  .header__email-link {
    margin-right: 3rem;
  }

  /* intro */
  .intro {
    background-position: bottom;
  }

  .intro__inner {
    padding: 9.2rem 0 15.4rem 0;
  }

  .intro__text {
    max-width: 43rem;
  }

  .intro__text br {
    display: none;
  }

  /* CONTACTS=================== */
  .intro-contacts {
    background-size: 50%;
  }

  .intro-contacts__text-block {
    max-width: 55rem;
  }

  .intro-contacts__text-block br {
    display: none;
  }

  /* rates */
  .rates__cards,
  .cars__cards {
    flex-wrap: wrap;
    justify-content: center
  }

  .rates__cards-item,
  .rates__cards-viewall {
    flex: 0 1 29rem;
    justify-content: center
  }

  .rates__item-image img {
    width: 100%;
  }

  /* footer */
  .footer__logo img {
    max-width: 16rem;
  }

  .footer__content {
    margin-left: 7rem;
  }

  .footer__menu-list {
    gap: 4rem;
  }

  .footer__email-link {
    margin-right: 3rem;
  }
}

@media (max-width: 1100px) {

  /* header */
  .header__logo img {
    max-width: 14rem;
  }

  .header__content {
    margin-left: 6rem;
  }

  .header__menu,
  .header__email-link {
    font-size: 1.4rem;
  }

  /* advantages */
  .advantages__content {
    gap: 5.5rem;
  }

  /* terms */
  .terms__caption {
    font-size: 2rem;
  }

  /* feebdback */
  .feedback__image {
    margin-right: 1.6rem;
  }

  /* header */
  .footer__logo img {
    max-width: 14rem;
  }

  .footer__content {
    margin-left: 6rem;
  }

  .footer__menu,
  .footer__email-link {
    font-size: 1.4rem;
  }
  /* CONTACTS============== */
  .contacts__info {
    margin-left: 5rem;
  }
}

@media screen and (max-width: 991px) {

  /* header */
  .header__content {
    justify-content: flex-end;
    gap: 4rem;
  }

  .header__menu {
    position: fixed;
    top: 0;
    left: 100%;
    height: 100%;
    width: 100%;
    background-color: #014493;
    padding: 8rem 2.7rem 0 0;
    display: flex;
    justify-content: flex-end;
    transition: all 0.4s ease;
    overflow: auto;
  }

  .header__menu.active {
    left: 0%;
  }

  .header__menu-list {
    flex-direction: column;
    align-items: flex-end;
    gap: 4rem;
    padding: 3rem 0;
  }

  .header__list-item {
    padding: 0;
  }

  .header__menu {
    font-size: 3rem;
  }

  /* advantages */
  .advantages__content {
    flex-wrap: wrap;
    justify-content: center;
  }

  .advantages__item {
    flex: 0 0 40%;
  }

  /* autopark */
  .autopark__content {
    flex-wrap: wrap;
    justify-content: center
  }

  .autopark__item {
    flex: 0 1 45%;
  }

  .autopark__item-image img {
    width: 100%;
  }

  .autopark__item-params {
    font-size: 2.2rem;
    line-height: 3.2rem;
  }

  .param-left--title {
    font-size: 3rem;
    line-height: 3.8rem;
  }

  .autopark__item-price {
    font-size: 2.8rem;
    line-height: 3.5rem;
  }

  .autopark__item-price--num {
    font-size: 3.6rem;
    line-height: 4.6rem;
  }

  .autopark__item-class {
    font-size: 1.8rem;
    line-height: 2.6rem;
  }

  /* terms */
  .terms__caption {
    font-size: 2.2rem;
  }

  .terms__content {
    flex-wrap: wrap;
    gap: 2rem;
  }

  .terms__item {
    flex: 1 1 100%;
  }

  /* feedback */
  .feedback__image {
    flex: 0 0 9.4rem;
    margin-right: 2.6rem;
  }

  .feedback__content {
    grid-template-columns: repeat(2, minmax(29rem, 50%));
    gap: 2rem 3rem;
  }

  /* footer */
  .footer__inner {
    padding-bottom: 4rem;
  }

  .footer__content {
    margin-left: 0;
    flex-direction: column;
    align-items: flex-end
  }

  .footer__menu-list {
    padding: 3rem 0 4rem 0;
  }

  .footer .footer__menu-link--bottom {
    top: 6rem;
    left: 0;
    display: inline-block;
    width: 100%;
  }

  /* CONTACTS================== */
  .contacts__inner {
    flex-direction: column-reverse;
  }

  .contacts__form {
    margin: 0 auto;
    width: 100%;
    max-width: 62rem;
  }

  .contacts__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    margin-left: 0;
  }
  .contacts__info-list {
    width: fit-content;
  }
  .contacts__social-list {
    margin-bottom: 2rem;
  }

  /* PLANS================== */
  .plans__item-title {
    padding: 2rem 0 2rem 2rem;
  }

  .plans-row {
    padding: 2rem 1rem 2rem 2rem;
  }

  .plans__econom-head,
  .plans__middle-head,
  .plans__business-head,
  .plans__cross-head,
  .plans__minivan-head,
  .plans__car-name,
  .plans__car-year,
  .plans__car-days {
    font-size: 1.6rem;
    line-height: 2rem;
  }

  /* CARS=============== */
  .cars-autopark__content {
    grid-template-columns: repeat(4, minmax(14rem, 25%));
  }

  .cars-autopark__item-param .param-left--title {
    font-size: 1.8rem;
    line-height: 2.5rem;
  }
}

@media screen and (max-width: 850px) {

  .plans__econom-head,
  .plans__middle-head,
  .plans__business-head,
  .plans__cross-head,
  .plans__minivan-head,
  .plans__car-name,
  .plans__car-year,
  .plans__car-days {
    font-size: 1.4rem;
    line-height: 2rem;
  }

  /* CARS============== */
  .cars__cards-item,
  .cars__cards-viewall {
    flex: 0 1 30rem;
  }

  .cars-autopark__item-param .param-left--title {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
}

@media screen and (max-width: 767px) {

  /* intro */
  .intro__inner {
    padding: 7rem 0 12rem 0;
  }

  .intro__location {
    flex-wrap: wrap;
  }

  .intro__location-list {
    margin-top: 1rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  .intro__title {
    font-size: 3rem;
    line-height: 3.8rem;
  }

  .intro__subtitle {
    font-size: 2rem;
    line-height: 2.8rem;
  }

  /* CONTACTS===================== */
  /* intro-contacts */
  .intro-contacts__location {
    flex-wrap: wrap;
  }

  .intro-contacts__location-list {
    margin-top: 1rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }

  .intro__title {
    font-size: 3rem;
    line-height: 3.8rem;
  }

  /* advantages */
  .advantages__content {
    gap: 4.5rem;
  }

  /* autopark */
  .autopark__item-params {
    font-size: 2rem;
    line-height: 3rem;
  }

  .param-left--title {
    font-size: 2.4rem;
    line-height: 3rem;
  }

  .autopark__item-price {
    font-size: 2.4rem;
    line-height: 3rem;
  }

  .autopark__item-price--num {
    font-size: 3.2rem;
    line-height: 4rem;
  }

  .autopark__item-class {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }

  /* feedback */
  .feedback__content {
    grid-template-columns: repeat(1, minmax(29rem, 100%));
    gap: 2rem 3rem;
  }

  /* connect */
  .form {
    margin: 0 auto;
  }

  .connect__inner {
    padding: 4rem;
  }
  /* footer */
  .footer__menu-list {
    margin-left: 2rem;
  }
  .footer .footer__menu-link--bottom {
    left: 2rem;
  }

  /* CONTACTS================= */
  .contacts__inner {
    padding-bottom: 5.5rem;
  }

  .intro-contacts__inner {
    padding: 4.7rem 0 0 0;
  }

  /* PLANS================== */
  .plans__title {
    margin-bottom: 4rem;
  }

  .intro-plans__inner,
  .intro-cars__inner {
    padding: 4.7rem 0 4.7rem 0;
  }

  .plans__item-title {
    font-size: 1.8rem;
    line-height: 2.4rem;
  }

  .plans-row {
    display: grid;
    grid-template-columns: minmax(14rem, 32rem) minmax(9rem, 20rem) repeat(6, minmax(6rem, 12rem));
    column-gap: 0.5rem;
  }

  .plans__econom-head,
  .plans__middle-head,
  .plans__business-head,
  .plans__cross-head,
  .plans__minivan-head,
  .plans__car-name,
  .plans__car-year,
  .plans__car-days {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }

  /* CARS======================== */
  .cars__cards-item,
  .cars__cards-viewall {
    flex: 0 1 45rem;
  }

  .cars__item-image {
    float: left;
  }

  .cars__cards-viewall {
    flex-direction: row;
    justify-content: center;
    gap: 3rem;
    min-height: 17.4rem;
    padding: 3.2rem 2rem;
  }

  .cars__cards-viewall--img {
    margin-bottom: 0rem;
  }

  .cars-autopark__content {
    grid-template-columns: repeat(3, minmax(14rem, 33%));
  }

  .cars-autopark__item-param .param-left--title {
    font-size: 1.8rem;
    line-height: 2.5rem;
  }

  .button.cars-autopark__button {
    height: 4rem;
    font-size: 1.6rem;
  }
}

@media screen and (max-width: 692px) {

  .plans__econom,
  .plans__middle,
  .plans__business,
  .plans__cross,
  .plans__minivan {
    overflow-x: scroll;
  }
}

@media screen and (max-width: 650px) {
  .container {
    padding: 0 1rem;
  }

  .text__content {
    font-size: 1.6rem;
    line-height: 2rem;
  }

  .button {
    padding: 0 4rem;
  }

  .title {
    font-size: 2.8rem;
    line-height: 3.8rem;
  }

  .chunk-title {
    font-size: 3.4rem;
    line-height: 4rem;
  }

  /* header */
  .header__logo img {
    max-width: 12rem;
  }

  .header__content {
    margin-left: 0;
  }
  .header__button {
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
  }


  /* intro */
  .intro__button {
    height: 4rem;
  }

  /* advantages */
  .advantages__item {
    flex: 0 0 100%;
  }

  .advantages__content {
    gap: 3.5rem;
  }

  /* autopark */
  .autopark__item {
    flex: 0 1 80%;
  }

  .autopark__link a::after {
    width: 17.6rem;
    left: -1rem;
  }

  /* terms */
  .terms__caption {
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }

  .terms .container {
    padding: 0 3rem;
  }

  /* connect */
  .form__button {
    width: 20rem;
    height: 4rem;
    font-size: 1.8rem;
    line-height: 2.4rem;
  }

  form input,
  textarea {
    padding: 0.8rem 0.8rem 0.8rem 1.2rem;
  }

  .form {
    padding: 4rem 5rem;
  }

  /* footer */
  .footer__inner {
    flex-direction: column-reverse;
  }

  .footer__logo {
    padding: 4.2rem 0 0 0;
  }
  .footer__menu-list {
    margin-left: 0;
  }
  .footer .footer__menu-link--bottom {
    left: 0;
  }
  .footer .header__btn {
    margin: 0 auto;
  }

  /* CONTACTS================== */
  .contacts__form {
    padding: 4rem 5rem;
  }
  .contacts__info-text {
    font-size: 1.8rem;
    line-height: 2.2rem;
  }

  /* PLANS */
  .plans__item {
    margin-top: 4rem;
  }

  .plans-row {
    grid-template-columns: minmax(10rem, 32rem) minmax(9rem, 20rem) repeat(6, minmax(6rem, 12rem));
  }

  /* CARS======================== */
  .cars-autopark__content {
    grid-template-columns: repeat(2, minmax(14rem, 24rem));
    justify-content: center
  }
  .cars-autopark__item-price {
    font-size: 1.6rem;
  }
  .cars-autopark__item-price--num {
    font-size: 2.4rem;
  }
}

@media screen and (max-width: 551px) {
  .form__title {
    font-size: 2.2rem;
    line-height: 3rem;
  }

  .form {
    padding: 3rem 4rem;
  }

  .form__descr {
    font-size: 1rem;
    line-height: 1.4rem;
  }

  .form__descr br {
    display: none;
  }

  /* footer */
  .footer__menu-list {
    gap: 2rem;
  }

  .footer__phone-link {
    left: 2.5rem;
  }

  /* CONTACTS ===========================*/
  .contacts__info {
    margin-bottom: 3rem;
  }

  .contacts__info {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .contacts__info-worktime {
    margin-bottom: 2rem;
  }

  .contacts__form {
    padding: 3rem 4rem;
  }

  /* PLANS================= */
  .plans__item {
    margin-top: 3rem;
  }
  /* CARS============ */
  .cars-autopark__item-param .param-left--title {
    font-size: 1.6rem;
    line-height: 2.2rem;
  }
  /* POPUP============= */
  .popup__wrapper,
  .rent-popup__wrapper {
    width: 30rem;
  }
  .popup__window,
  .rent-popup__window {
    padding: 3rem 2.2rem;
  }
  .popup__title,
  .rent-popup__title {
    font-size: 2rem;
    line-height: 2.8rem;
    margin-bottom: 2rem;
  }
  .rent-popup__calculate-car {
    font-size: 1.8rem;
  }
  .popup__classes-button, 
  .popup__cars-button {
    height: 4rem;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    line-height: 1.4rem;
    margin-bottom: 1rem;
  }
  .popup__calculate-day-price, 
  .popup__calculate-day-amount, 
  .popup__calculate-guarantie,
  .rent-popup__calculate-day-price, 
  .rent-popup__calculate-day-amount, 
  .rent-popup__calculate-guarantie {
    font-size: 1.4rem;
    line-height: 2rem;
    padding-left: 0;
  }
  .popup__calculate-button,
  .popup-rent__form-button {
    margin: 1rem 0;
    height: 3rem;
    font-size: 1.4rem;
    line-height: 2rem;
  }
  .popup__calculate-total,
  .rent-popup__calculate-total {
    font-size: 1.6rem;
    line-height: 2.7rem;
  }
  .popup-rent__form-name, 
  .popup-rent__form-phone {
    height: 3rem;
  }
  .popup-rent__form-name input, 
  .popup-rent__form-phone input {
    padding: 0.5rem 1rem;
}
}

@media screen and (max-width: 468px) {
  .inner {
    padding: 3rem 0;
  }

  .title {
    font-size: 2.4rem;
    line-height: 3.2rem;
  }

  /* header */
  .header__logo img {
    max-width: 10rem;
  }

  /* intro */
  .intro__inner {
    padding: 4rem 0 5rem 0;
  }

  .intro__location {
    margin-bottom: 2.6rem;
  }

  .intro__location-list {
    gap: 1rem;
    font-size: 1.2rem;
  }

  /* connect */
  .connect__inner {
    padding: 2rem;
  }
  /* footer */
  .footer__menu-link {
    font-size: 1.1rem;
    gap: 1.5rem;
    white-space: nowrap;
  }

  .form {
    padding: 3rem 2rem 3rem 2rem;
  }

  
  /* intro-contacts */
  .intro__location {
    margin-bottom: 2.6rem;
  }

  .intro-contacts__location-list {
    gap: 1rem;
    font-size: 1.2rem;
  }

  .autopark__item {
    flex: 0 1 90%;
  }
  /* CONTACTS ===========================*/
  .contacts__form {
    padding: 3rem 2rem 3rem 2rem;
  }
  .contacts__info-text {
    font-size: 1.4rem;
    line-height: 2rem;
  }
}

@media screen and (max-width: 375px) {
  .title {
    font-size: 2rem;
    line-height: 2.6rem;
  }
  /* header */
  .header__btn {
    margin-left: 1rem;
  }
  .header__content {
    gap: 2rem;
  }

  /* autopark */

  .autopark__item {
    flex: 0 1 100%;
  }

  /* footer */
  .footer__menu,
  .footer__email-link {
    width: 100%;
  }

  .footer__menu-list {
    flex-direction: column;
    align-items: center
  }

  .footer .footer__menu-link--bottom {
    font-size: 1.1rem;
    white-space: nowrap;
    top: 23rem;
    left: -9%;
    width: 100%;
  }

  .footer__phone-link {
    left: 0;
  }
  /* CARS============= */
  .cars-autopark__item-price {
    font-size: 1.4rem;
  }
  .cars-autopark__item-price--num {
    font-size: 2rem;
  }
  .cars-autopark__item-params {
    font-size: 1.2rem;
  }
}