.footer {
  background-color: #014493;
  width: 100%;
}
.footer a {
  color: #ffffff;
}
.footer__inner {
  font-family: $secondFont;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.footer__logo {
  padding: 4.2rem 0 4.8rem 0;
}
.footer__logo img {
  max-width: 18.8rem;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  flex: 1 1 100%;
  align-items: center;
  margin-left: 9.2rem;
}
.footer__menu {
  position: relative;
}
.footer__menu-list {
  display: flex;
  gap: 5rem;
}
.footer__menu-item {
  text-align: center;
}
.footer__menu-link {
}

.footer .footer__menu-link--bottom {
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.9rem;
  color: #4F89CD;
  position: absolute;
  top: 4.2rem;
}