/* Общие стили элементов */

.inner {
  padding: 5.5rem 0;
}

.text__content {
  font-size: 1.8rem;
  line-height: 2.3rem;
}
.title {
  font-family: $thirdFont;
  display: inline-block;
  font-size: 3.4rem;
  line-height: 4.1rem;
  text-transform: uppercase;
  font-weight: 700;
  color: #014493;
}
.chunk-title {
  font-family: $thirdFont;
  font-weight: 700;
  font-size: 4rem;
  line-height: 4.8rem;
  text-transform: uppercase;
  color: #014493;
}
.button {
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  padding: 0 5.9rem;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #FF2219;
  color: #fff;
}
.form__input-name {
  margin: 3rem 0 2rem 0;
}

.form__textarea {
  margin: 2rem 0 3rem 0;
  height: 9rem;
  resize: vertical;
}
.form__button {
  width: 30rem;
  height: 5rem;
  margin: 0 auto;
  font-family: $secondFont;
  background-color: #FF2219;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.7rem;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
.form__descr {
  font-family: $secondFont;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-align: center;
  color: #333333;
}
form input, textarea {
  border-radius: 1rem;
  outline: none;
  border: none;
  padding: 1.3rem 0 1.3rem 3.2rem;
}
input::placeholder, 
textarea::placeholder {
  font-family: $secondFont;
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: rgba(51, 51, 51, 0.5);
}